import { useState } from "react";

const fruitBasket = [
  { id: 1, name: "Mango", price: 60, quantity: 0 },
  { id: 2, name: "Apple", price: 100, quantity: 0 },
  { id: 3, name: "Banana", price: 40, quantity: 0 },
  { id: 4, name: "Strawberry", price: 150, quantity: 0 },
  { id: 5, name: "Papaya", price: 20, quantity: 0 }
];
export function Products() {
  const [basket, setBasket] = useState(fruitBasket);
  const [showInput, setShowInput] = useState(false);
  const [fruitName, setFruitName] = useState();
  const [price, setPrice] = useState();
  const [quantity, setQuantity] = useState();
  const [countID, setCountID] = useState(6);
  function incrementBasket(obj) {
    setBasket(
      basket.map((fruit) =>
        fruit.id === obj.id ? { ...fruit, quantity: fruit.quantity + 1 } : fruit
      )
    );
  }
  function decrementBasket(obj) {
      setBasket(
        basket.map((fruit) =>
          fruit.id === obj.id ? { ...fruit, quantity: fruit.quantity - 1 } : fruit
        )
      );
    
  }
  const removeFromBasket = (obj) => {
    setBasket(basket.filter((fruit) => (fruit.id === obj.id ? !fruit : fruit)));
  };
  return (
    <>
      <div>
        <button onClick={() => setShowInput(true)} className="secondary-btn">
          Add Fruits
        </button>
        {showInput && (
          <div className="input-div">
            <input
              placeholder="Enter Fruit Name"
              className="input-textArea"
              onChange={(e) => setFruitName(e.target.value)}
              value={fruitName}
            ></input>
            <input
              placeholder="Enter Price"
              className="input-textArea"
              onChange={(e) => setPrice(e.target.value)}
              value={price}
            ></input>
            <input
              placeholder="Enter Quantity"
              className="input-textArea"
              onChange={(e) => setQuantity(e.target.value)}
              value={quantity}
            ></input>
            <button
              onClick={() => {
                setCountID((prev) => prev + 1);
                setBasket([
                  ...basket,
                  {
                    id: countID,
                    name: fruitName,
                    price: price,
                    quantity: quantity
                  }
                ]);
                setShowInput(false);
              }}
              className="primary-btn"
            >
              Add
            </button>
          </div>
        )}
      </div>
      <ul className="fruitList">
        {basket.map((obj) => {
          return (
            <li className="list" key={obj.id}>
              {obj.name}
              <p className="price">Rs. {obj.price}</p>
              <button
                className="primary-btn"
                onClick={() => incrementBasket(obj)}
              >
                +
              </button>
              {obj.quantity}
              <button
                className="primary-btn"
                onClick={() => decrementBasket(obj)}
                disabled={obj.quantity===0}
              >
                -
              </button>
              <div>
                <button
                  onClick={() => removeFromBasket(obj)}
                  className="secondary-btn"
                >
                  Remove
                </button>
              </div>
            </li>
          );
        })}
      </ul>
    </>
  );
}
